.Page {
    width: 100%;
    height: 100%;
    //padding: 10px;
}
div.OldMenu {
    width: 100%;
    height: 100%;
    max-width: 360px;
    position: fixed;
}

div.OldMenu ul {
    list-style: none;
    padding-left: 20px;
}

div.OldMenu ul li {
    /*box-sizing: border-box;*/
    padding: 0.5em;
}

div.OldMenu ul li:not(.MenuDropdown-toggle) {
    display: -webkit-flex;
    display: flex;
}

div.OldMenu ul li:hover {
    background-color: var(--primary-dark);
}

div.OldMenu a {
    text-decoration: none;
    color: #000000;
    width: 100%;
}

/* clickable div for OldMenu dropdown toggle */
div.OldMenu li.MenuDropdown-toggle > div {
    cursor: pointer;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

div.OldMenu div.MenuDropdown-content {
    -webkit-transition: height 0.3s;
    transition: height 0.3s;
    overflow: hidden;
}

div.OldMenu div.MenuDropdown-content > ul > li {
    padding-left: 20px;
    margin-left: -20px;
}
html {
    height: 100%;
}

